
.app {
    height: auto;
    margin: 48px , auto, 80px, auto;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    color: #ffffff;
}

.todo-list {
    margin-top: 16px;
    height: 48px;
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: #ffffff;
}

li {
    list-style: none;
    overflow-wrap: anywhere;
    height: auto;
    width: 176px;
}

